.nav-link
{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    margin-right: 40px !important;
    color: #FFFFFF !important;
}

.navbar
{
    width: 100vw;
    z-index: 1000;
}

.navbar-dark .navbar-brand
{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    color: #FFFFFF !important;
    padding: 0;
}

.navbar-expand-md .navbar-brand
{
    margin-right: 40px !important;
}

.navbar-expand-md .navbar-brand > img
{
    margin-right: 40px;
}