@import url(https://fonts.googleapis.com/css?family=Roboto:500&display=swap);
@font-face {
    font-family: 'Gravity Regular';
    font-style: normal;
    font-weight: normal;
    src:
        local('Gravity Regular'),
        url(/website/static/media/Gravity-Regular.c5833b37.otf) format('opentype');
}

@font-face {
    font-family: 'Gravity Bold';
    font-style: Bold;
    font-weight: bold;
    src:
        local('Gravity Bold'),
        url(/website/static/media/Gravity-Bold.81cc224b.otf) format('opentype');
}

.bgMain
{
    width: 100vw;
    height: 100vh;

    padding: 0px 15px;

    background: rgba(0,0,0,0.6);
    background-image: url(/website/static/media/main_wallpaper.fadf5b8e.jpg);
    background-position: top center;
    background-size: cover;
    background-blend-mode: darken;
}

.presentationBox
{
    position: absolute;

    height: 32vh;
}

.title
{
    color: #fff;
    text-align: left;
    text-shadow: 5px 5px 5px #000;
    
    font-family: 'Gravity Regular';
    font-size: 5vh;
    letter-spacing: 0.5em;
}

.presentation
{
    color: #fff;
    text-align: left;
    text-shadow: 5px 5px 5px #000;
    
    font-family: 'Gravity Regular';
    font-size: 3vh;
}

.my-work-header
{
    background-image: url(/website/static/media/my-work.85b3a637.png);

    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: left top;
}

.my-work-header-title
{
    color: white;

    font-family: 'Gravity Bold';
    font-size: 2.5vh;
    letter-spacing: 0.625em;

    margin-top: 3vh;
}

.my-work-header-text
{
    color: white;
    
    font-family: 'Gravity Bold';
    font-size: 1vh;
    letter-spacing: 0.5em;
}

@media (min-width: 768px)
{
    .tiling > .col-md-12
    {
        height: 20vh;
    }

    .my-work-header-title
    {
        color: white;

        font-family: 'Gravity Bold';
        font-size: 28px;
        letter-spacing: 0.625em;

        margin-top: 3vh;
    }

    .my-work-header-text
    {
        color: white;
        
        font-family: 'Gravity Bold';
        font-size: 14px;
        letter-spacing: 0.5em;
    }
}

@media (min-width: 992px)
{
    .tiling > .col-lg-6
    {
        height: 40vh;
    }

    .my-work-header-title
    {
        color: white;

        font-family: 'Gravity Bold';
        font-size: 38px;
        letter-spacing: 0.625em;

        margin-top: 3vh;
    }

    .my-work-header-text
    {
        color: white;
        
        font-family: 'Gravity Bold';
        font-size: 20px;
        letter-spacing: 0.5em;
    }
}
.nav-link
{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    margin-right: 40px !important;
    color: #FFFFFF !important;
}

.navbar
{
    width: 100vw;
    z-index: 1000;
}

.navbar-dark .navbar-brand
{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    color: #FFFFFF !important;
    padding: 0;
}

.navbar-expand-md .navbar-brand
{
    margin-right: 40px !important;
}

.navbar-expand-md .navbar-brand > img
{
    margin-right: 40px;
}
@media (min-width: 992px)
{
    .tile-square
    {
        width: 25vw;
        height: 40vh;
        padding: 5vh;
    }
}
@media (max-width: 991px)
{
    .tile-square
    {
        width: 50vw;
        height: 20vh;
        padding: 2vh;
    }
}

.tile-square
{
    background-size: cover;
    background-position: center bottom;
}

.tile-square h3
{
    font-family: 'Gravity Regular';
    font-size: calc(1vh + 1vw);
}

.tile-square li
{
    font-family: 'Gravity Regular';
    font-size: calc(0.65vh + 0.75vw);
}

.Tile
{
    display: flex;
}
