@import url('https://fonts.googleapis.com/css?family=Roboto:500&display=swap');

@font-face {
    font-family: 'Gravity Regular';
    font-style: normal;
    font-weight: normal;
    src:
        local('Gravity Regular'),
        url('./fonts/Gravity/Gravity-Regular.otf') format('opentype');
}

@font-face {
    font-family: 'Gravity Bold';
    font-style: Bold;
    font-weight: bold;
    src:
        local('Gravity Bold'),
        url('./fonts/Gravity/Gravity-Bold.otf') format('opentype');
}

.bgMain
{
    width: 100vw;
    height: 100vh;

    padding: 0px 15px;

    background: rgba(0,0,0,0.6);
    background-image: url('./Images/main_wallpaper.jpg');
    background-position: top center;
    background-size: cover;
    background-blend-mode: darken;
}

.presentationBox
{
    position: absolute;

    height: 32vh;
}

.title
{
    color: #fff;
    text-align: left;
    text-shadow: 5px 5px 5px #000;
    
    font-family: 'Gravity Regular';
    font-size: 5vh;
    letter-spacing: 0.5em;
}

.presentation
{
    color: #fff;
    text-align: left;
    text-shadow: 5px 5px 5px #000;
    
    font-family: 'Gravity Regular';
    font-size: 3vh;
}

.my-work-header
{
    background-image: url('./Images/my-work.png');

    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: left top;
}

.my-work-header-title
{
    color: white;

    font-family: 'Gravity Bold';
    font-size: 2.5vh;
    letter-spacing: 0.625em;

    margin-top: 3vh;
}

.my-work-header-text
{
    color: white;
    
    font-family: 'Gravity Bold';
    font-size: 1vh;
    letter-spacing: 0.5em;
}

@media (min-width: 768px)
{
    .tiling > .col-md-12
    {
        height: 20vh;
    }

    .my-work-header-title
    {
        color: white;

        font-family: 'Gravity Bold';
        font-size: 28px;
        letter-spacing: 0.625em;

        margin-top: 3vh;
    }

    .my-work-header-text
    {
        color: white;
        
        font-family: 'Gravity Bold';
        font-size: 14px;
        letter-spacing: 0.5em;
    }
}

@media (min-width: 992px)
{
    .tiling > .col-lg-6
    {
        height: 40vh;
    }

    .my-work-header-title
    {
        color: white;

        font-family: 'Gravity Bold';
        font-size: 38px;
        letter-spacing: 0.625em;

        margin-top: 3vh;
    }

    .my-work-header-text
    {
        color: white;
        
        font-family: 'Gravity Bold';
        font-size: 20px;
        letter-spacing: 0.5em;
    }
}