@media (min-width: 992px)
{
    .tile-square
    {
        width: 25vw;
        height: 40vh;
        padding: 5vh;
    }
}
@media (max-width: 991px)
{
    .tile-square
    {
        width: 50vw;
        height: 20vh;
        padding: 2vh;
    }
}

.tile-square
{
    background-size: cover;
    background-position: center bottom;
}

.tile-square h3
{
    font-family: 'Gravity Regular';
    font-size: calc(1vh + 1vw);
}

.tile-square li
{
    font-family: 'Gravity Regular';
    font-size: calc(0.65vh + 0.75vw);
}

.Tile
{
    display: flex;
}